<template>
  <div class="detail">
    <van-nav-bar
      left-text="返回"
      right-text="首页"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />
    <img :src="product.coverImage | dalImg" alt="" />
    <div class="content">
      <p>￥{{ product.price }}</p>
      <h2>{{ product.name }}</h2>
    </div>

    <p class="bbxq">宝贝详情</p>
    <div class="content" v-html="product.content"></div>
    <van-goods-action>
      <van-goods-action-icon icon="chat-o" text="客服" @click="kefu" />
      <van-goods-action-icon icon="cart-o" text="购物车" @click="toCart" />
      <van-goods-action-icon icon="shop-o" text="店铺" @click="toHome" />
      <van-goods-action-button
        type="danger"
        text="加入购物车"
        @click="onClickButton"
      />
    </van-goods-action>
  </div>
</template>

<script>
import { Toast } from "vant";
import { loadproductDetailAPI } from "../services/products";
import { loadAddCart } from "../services/products";

export default {
  data() {
    return {
      product: {},
    };
  },

  async created() {
    const resProduct = await loadproductDetailAPI(this.$route.params.id);
    this.product = resProduct.data;
   
  },

  methods: {
    //加入购物车
    async onClickButton() {
      const res = await loadAddCart(this.product.id, 1, this.product.price)
      console.log(res);
      Toast.success(res.msg)
    },

    kefu() {
      Toast.success("暂未开通");
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
      this.$router.push({
        name: "home",
      });
    },
    toCart() {
      this.$router.push({
        name: "cart",
      });
    },
    toHome() {
      this.$router.push({
        name: "home",
      });
    },
  },
};
</script>
<style scoped>
.detail {
  background-color: #dfe3eb;
  display: flex;
  flex-direction: column;
}
.bbxq {
  margin: 0 auto;
}

.content {
  background-color: white;
  margin: 10px;
  border-radius: 10px;
  padding: 10px;
}
.content p {
  font-size: 24px;
  color: tomato;
}
</style>



